.cart-base-item {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: center;
  margin: 10px;
  background-color: black;
  border: 10px double magenta;
  padding: 20px;
  width: 250px;
}
.cart-base-item p {
  font-size: 1.25em;
  font-weight: 700;
  color: white;
}

.cart-base-item Button {
  width: 100px;
  height: 100px;
  border-radius: 50px;
  margin: 10px;
  background-color: red;
  border: 7px double black;
  outline: none;
  font-size: 1.25em;
  color: white;
}
.cart-base-item .bin {
  background-color: black;
  display: flex;
  justify-content: space-evenly;
  padding: 15px;
}
.counter {
  background-color: rgb(9, 172, 231);
  display: flex;
  flex-direction: row;
  justify-content: center;
  position: relative;
  align-items: center;
  width: 250px;
  font-size: calc(16px + 1vw);
  font-family: "Roboto";
}
.counter .add2 {
  position: relative;
  right: 30px;
  background-color: white;
  width: 40px;
  height: 40px;
  color: black;
  font-weight: 900;
  font-size: calc(16px + 0.5vw);
  font-family: "Roboto";
}
.counter .minus2 {
  position: relative;
  left: 30px;
  background-color: white;
  width: 40px;
  height: 40px;
  color: black;
  font-weight: 900;
  font-size: calc(16px);
  font-family: "Roboto";
}
